import { BigNumber } from 'ethers';
import { Utils } from '../services/Utils';

export interface IItemDetails {
  balance: number;
  id: number;
  price: BigNumber;
  maxSupply: BigNumber;
  itemPayable: boolean;
  type: number;
  name: string;
  image: string;
  animation: string;
  paymentContract: string;
  mintingActive: boolean;
}

export async function toItemDetails(details: Array<any>): Promise<IItemDetails> {
  const imageUrl = details[4];
  const itemId = +imageUrl.split('/').pop().split('.')[0];
  return {
    balance: 0,
    id: itemId,
    price: details[0],
    maxSupply: details[1],
    itemPayable: details[2],
    name: details[3],
    image: details[4].replace('https://farmland-game.github.io/assets/', './assets/'),
    animation: details[5].replace('https://farmland-game.github.io/assets/', './assets/'),
    paymentContract: details[6],
    mintingActive: details[7],
    type: +Utils.formatUnits(details[8], 0)
  };
}
