import React, { Component } from 'react';
import { ICharacterDetails } from '../../models/CharacterDetails';
import { Swiper, SwiperSlide } from 'swiper/react/swiper-react.js';
import { IQuestService } from '../../services/QuestService';
import { Explorer } from './Explorer';
import { ItemNftService } from '../../services/ItemNftService';
import { EthereumService } from '../../services/EthereumService';
import { BeginQuest } from './BeginQuest';

export interface IQuestProps {
  questService: IQuestService;
  characters: ICharacterDetails[];
  itemService?: ItemNftService;
  ethereumService: EthereumService;
}

export interface IQuestState {
  employ?: ICharacterDetails | null;
  beginQuest?: ICharacterDetails | null;
}

export class Quest extends Component<IQuestProps, IQuestState> {
  navigationPrevRef: any;
  navigationNextRef: any;

  constructor(props: any) {
    super(props);
    this.state = {
      employ: null,
      beginQuest: null
    };

    this.navigationPrevRef = React.createRef();
    this.navigationNextRef = React.createRef();
  }

  render() {
    return (
      <div className="explorers">
        <div className="table-control">
          <table className="table table-grouped is-fullwidth">
            <tbody>
              {this.props.characters.map(c => (
                <Explorer
                  key={c.index}
                  itemService={this.props.itemService}
                  questService={this.props.questService}
                  character={c}
                  ethereumService={this.props.ethereumService}></Explorer>
              ))}
            </tbody>
          </table>
        </div>
      </div>
    );
  }

  render2() {
    return (
      <React.Fragment>
        <Swiper className="px-1" navigation spaceBetween={10} slidesPerView="auto" centerInsufficientSlides>
          {this.props.characters.map(character => (
            <SwiperSlide key={character.name + character.index}>
              <Explorer
                key={character.index}
                itemService={this.props.itemService}
                questService={this.props.questService}
                character={character}
                ethereumService={this.props.ethereumService}></Explorer>
            </SwiperSlide>
          ))}
        </Swiper>
        {this.state.beginQuest != null && (
          <BeginQuest
            questService={this.props.questService}
            character={this.state.beginQuest}
            close={() => this.setState({ beginQuest: null })}></BeginQuest>
        )}
      </React.Fragment>
    );
  }
}
