import { Token } from '@uniswap/sdk';
import { BigNumber, ethers } from 'ethers';
import { IAssetConfig } from '../models/AssetConfig';
import { EthereumService } from './EthereumService';
import { MessagingService } from './MessagingService';
import { INonFungibleAssetService } from './Interfaces';
import { IItemDetails, toItemDetails } from '../models/ItemDetails';
import { Map } from 'immutable';

export class ItemNftService implements INonFungibleAssetService {
  walletAddress?: string;
  contract?: ethers.Contract;
  allItems: IItemDetails[] = [];
  itemMap: Map<number, IItemDetails> = Map<number, IItemDetails>();
  count: number = 0;
  gasPrice?: string | number;
  token: Token;
  tokenUri?: string;

  // TODO interface needs refactoring, some of this doesn't make sense

  nfts: any[] = [];
  actionText: string = 'Find';
  actionUrl: string = '/quests';

  constructor(
    public config: IAssetConfig,
    private messagingService: MessagingService,
    private ethereumService: EthereumService
  ) {
    this.walletAddress = this.ethereumService!.walletData.walletAddress;

    this.contract = new ethers.Contract(
      this.config.contractAddress,
      this.config.assetConstants!.contractAbi,
      ethereumService.signer
    );

    this.token = new Token(
      ethereumService.networkManager!.network!.chainId,
      this.config.contractAddress,
      this.config.assetConstants!.decimals
    );

    this.setupEventHandlers();
  }

  async update(): Promise<ItemNftService> {
    return await this.refreshNfts().then(() => this);
  }

  async loadStatics() {
    if (this.allItems.length > 0) {
      return;
    }

    var items = [];
    var itemsJson = localStorage.getItem('items');
    var itemsLoadedMs = localStorage.getItem('itemsLoaded') ?? Date.now();
    var now = Date.now();
    var expired = now - +itemsLoadedMs > 86400000;

    if (!itemsJson || expired) {
      var i0 = await this.contract!.listItems(0);
      var i1 = await this.contract!.listItems(1);
      items = i0.concat(i1);
      localStorage.setItem('items', JSON.stringify(items));
      localStorage.setItem('itemsLoaded', now.toString());
    } else {
      items = JSON.parse(itemsJson);
    }

    var allItems: IItemDetails[] = [];
    await items.forEach(async (item: Array<any>) => {
      var itemDetails = await toItemDetails(item);
      allItems.push(itemDetails);
      this.itemMap = this.itemMap.set(itemDetails.id, itemDetails);
    });

    this.allItems = allItems.filter(i => i.name);
  }

  async refreshNfts() {
    await this.loadStatics();

    var allItemIds = this.allItems.map((item: IItemDetails) => item.id);

    const details = await this.contract!.balanceOfBatch(
      allItemIds.map(_ => this.walletAddress),
      allItemIds
    );

    var itemBalances: number[] = details.map((bn: BigNumber) => bn.toNumber());
    this.allItems.forEach((i: IItemDetails) => {
      i.balance = itemBalances[i.id];
    });

    this.nfts = [];
    for (let index = 0; index < itemBalances.reduce((p, a) => p + a, 0); index++) {
      // @ts-ignore
      this.nfts.push({});
    }
  }

  removeEventHandlers() {
    if (this.contract) {
      this.contract!.removeAllListeners();
    }
  }

  setupEventHandlers() {
    this.removeEventHandlers();

    var update = (message: string) =>
      this.update().then(() => {
        this.messagingService.events.next({ message: message });
      });

    if (this.contract) {
      this.contract!.on(
        {
          address: this.ethereumService.walletData.walletAddress,
          topics: [ethers.utils.id('Transfer(address,address,uint256)')]
        },
        (from, to, tokenId) => {
          if (to === this.ethereumService.walletData.walletAddress) {
            update(`${this.config.assetConstants?.symbol} available`);
          }
        }
      );
    }
  }
}
