import { BigNumber } from '@ethersproject/bignumber';
import { Component } from 'react';
import { CharacterStat, ICharacterDetails } from '../../models/CharacterDetails';
import { Utils } from '../../services/Utils';
import { IQuestService } from '../../services/QuestService';

export interface IBeginQuestProps {
  questService: IQuestService;
  character: ICharacterDetails;
  close: any;
}

export interface IBeginQuestState {
  questLength: number;
}

export class BeginQuest extends Component<IBeginQuestProps, IBeginQuestState> {
  constructor(props: any) {
    super(props);

    this.state = {
      questLength: 0
    };

    this.handleAmountChange = this.handleAmountChange.bind(this);
    this.getCurrentAmount = this.getCurrentAmount.bind(this);
    this.beginQuest = this.beginQuest.bind(this);
    this.getQuestCost = this.getQuestCost.bind(this);
    this.close = this.close.bind(this);
  }

  handleAmountChange(amount: number) {
    this.setState({ questLength: this.state.questLength + amount });
  }

  getCurrentAmount(stat: CharacterStat): number {
    return this.props.character.stats.find(s => s.name === CharacterStat[stat])!.value;
  }

  beginQuest(nft: ICharacterDetails): void {
    if (this.state.questLength) {
      this.props.questService.beginQuest(this.props.character, this.state.questLength);
      this.close();
    }
  }

  getQuestCost(): string {
    return Utils.formatUnits(this.props.questService.questPrice?.mul(BigNumber.from(this.state.questLength)));
  }

  close() {
    this.props.close();
  }

  render() {
    return (
      <div className="is-active modal">
        <div className="modal-background" onClick={this.close}></div>
        <div className="modal-content begin-quest">
          <div className="card">
            <button className="modal-close is-large" aria-label="close" onClick={this.close}></button>
            <div className="is-flex is-justify-content-center begin-quest-title pb-3">{this.props.character.name}</div>
            <ul>
              {this.renderStatListItem(CharacterStat.Stamina)}
              {this.renderStatListItem(CharacterStat.Strength)}
              {this.renderStatListItem(CharacterStat.Speed)}
              {this.renderStatListItem(CharacterStat.Courage)}
              {this.renderStatListItem(CharacterStat.Intelligence)}
            </ul>
            <div className="is-flex is-flex-direction-column is-align-items-center is-justify-content-center mt-5">
              <p>
                <span className="mr-1">Quest Length</span>
              </p>
            </div>
            <div className="is-flex is-justify-content-center mt-4">
              <div className="is-flex is-flex-direction-column is-align-items-center is-justify-content-center character-stats">
                <div className="field has-addons">
                  <div className="control">
                    <button
                      className="button minus-button is-action"
                      disabled={this.state.questLength <= 0}
                      onClick={() => this.handleAmountChange(-1)}>
                      <span className="icon">
                        <i className="fas fa-minus"></i>
                      </span>
                    </button>
                  </div>
                  <div className="control">
                    <input className="input is-action" type="text" disabled={true} value={this.state.questLength} />
                  </div>
                  <div className="control">
                    <button
                      className="button plus-button is-action"
                      disabled={this.state.questLength >= this.props.questService.maxNumberOfQuests}
                      onClick={() => this.handleAmountChange(+1)}>
                      <span className="icon">
                        <i className="fas fa-plus"></i>
                      </span>
                    </button>
                  </div>
                </div>
                <button
                  className="button begin-button is-action is-fullwidth"
                  onClick={() => this.beginQuest(this.props.character)}
                  disabled={this.state.questLength <= 0}>
                  Begin Quest
                </button>
              </div>
            </div>
            <div className="is-flex is-flex-direction-column is-align-items-center is-justify-content-center mt-5">
              <p>
                <span className="mr-1">Cost {this.getQuestCost()}</span>
                {this.props.questService.questConfig.questPayConfig!.assetConstants!.symbol} (+ gas)
              </p>
            </div>
          </div>
        </div>
      </div>
    );
  }

  renderStatListItem(stat: CharacterStat) {
    return (
      <CharacterStatListItem currentStat={stat} currentValue={this.getCurrentAmount(stat)}></CharacterStatListItem>
    );
  }
}

interface ICharacterStatListItemProps {
  currentStat: CharacterStat;
  currentValue: number;
}

class CharacterStatListItem extends Component<ICharacterStatListItemProps> {
  render() {
    return (
      <li className="is-flex is-justify-content-space-between character-stat-list-item">
        <span className="is-flex is-justify-content-space-between" style={{ width: '100%' }}>
          <span>{CharacterStat[this.props.currentStat]}</span>
          <span>{this.props.currentValue}</span>
        </span>
      </li>
    );
  }
}
